<template>
  <div
    class="flex row"
    :class="
      invalid &&
        (answer === undefined ||
          answer == '')
        ? invalid
        : ''
    "
  >
    <input
      v-if="!valid"
      :id="qid + '_validator'"
      :key="valid"
      value="O máximo deve ser superior ao mínimo."
      type="text"
      oninvalid="setCustomValidity('O máximo deve ser superior ao mínimo.')"
      :pattern="valid ? undefined : '.a'"
      class="warning-field removable"
      onkeydown="return false;"
      :size="'O máximo deve ser superior ao mínimo.'.length-3"
      @invalid="invalid = 'invalid'"
    >
      
    <div
      v-for="(option, optionIndex) in result.options"
      :key="optionIndex"
      class="flex row la"
      style="margin-bottom: 5px;"
    >
      <input
        :id="qid+'_option_'+optionIndex"
        v-model="result.options[optionIndex]"
        type="text"
        style="height: 32px;"
        @change="update"
      >
      <i
        v-show="result.options.length > 1"
        class="remove round flex ca vca unselectable"
        style="margin-top: -1px;"
        @click="result.options.splice(optionIndex, 1)"
      >✖</i>
    </div>
    <button
      type="button"
      class="add_field add_section hoverable"
      @click="result.options.push('')"
    >
      Adicionar opção
    </button>
    <br>
    <div
      id="advanced_options_control"
      class="flex row unselectable"
      @click="advancedOptionsShown = !advancedOptionsShown"
    >
      <h2>Opções avançadas</h2>
      <i
        v-show="!advancedOptionsShown"
        class="arrow right"
      /><i
        v-show="advancedOptionsShown"
        class="arrow down"
      />
    </div>
    <div v-show="advancedOptionsShown">
      <h3>Valores por defeito: </h3>
      <div
        v-for="(option, optionIndex) in result.options"
        :key="optionIndex"
      >
        <label
          class="option-label no-width"
          :for="qid+'_default_'+optionIndex"
        >{{ option }} </label>
        <input 
          :id="qid+'_default_'+optionIndex"
          v-model="result.default"
          :value="optionIndex"
          type="checkbox"
          @change="update"
        >
      </div>
      <br>
      <label
        class="option-label"
        :for="qid+'_min_selected'"
      >Mínimo selecionados: </label>
      <input
        :id="qid+'_min_selected'"
        v-model.number="result.min_selected"
        type="number"
        step="1"
        min="1"
        @change="update"
      >
      <br>
      <label
        class="option-label"
        :for="qid+'_max_selected'"
      >Máximo selecionados: </label>
      <input
        :id="qid+'_max_selected'"
        v-model.number="result.max_selected"
        type="number"
        step="1"
        min="1"
        @change="update"
      >
      <br>
      <br>
      <label class="switch">
        <input
          v-model="result.allowed_other"
          type="checkbox"
          @change="update"
        >
        <span class="slider round allowed_other" />
      </label>
      <br>

      <div v-show="result.allowed_other">
        <label
          class="option-label"
          :for="qid+'_placeholder'"
        >Texto de sugestão (outros): </label>
        <input
          :id="qid+'_placeholder'"
          v-model="result.placeholder"
          type="text"
          @change="update"
        >
        <br>
        <label
          class="option-label"
          :for="qid+'_min_length'"
        >Tamanho mínimo (outros): </label>
        <input
          :id="qid+'_min_length'"
          v-model.number="result.min_length"
          type="number"
          step="1"
          min="1"
          @change="update"
        >
        <br>
        <label
          class="option-label"
          :for="qid+'_max_length'"
        >Tamanho máximo (outros): </label>
        <input
          :id="qid+'_max_length'"
          v-model.number="result.max_length"
          type="number"
          step="1"
          min="1"
          @change="update"
        >
        <br>
        <label
          class="option-label"
          :for="qid+'_decimal_places'"
        >Pergunta de outros: </label>
        <input
          :id="qid+'_other_string'"
          v-model="result.other_string"
          type="text"
          @change="update"
        >      
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "MultipleChoiceField",
  props: ["qid", "properties"],
  emits: ["update"],
  data() {
    return {
      result: this.properties,
      valid: true,
      invalid: "",
      advancedOptionsShown: false,
    };
  },
  created() {
    if (this.result.options === undefined) {
      this.result.options = [""];
    }
    if (this.result.default === undefined) {
      this.result.default = [];
    }
  },
  methods: {
    update() {
      this.valid = !this.result.min_length || !this.result.max_length || this.result.min_length <= this.result.max_length
      this.valid = (this.valid) && (this.valid = !this.result.min_selected || !this.result.max_selected || this.result.min_selected <= this.result.max_selected)
      if (this.result.default === "" || (this.result.default !== undefined && this.result.default.length == 0))
        this.result.default = undefined;
      if (this.result.placeholder === "" || !this.result.allowed_other)
        this.result.placeholder = undefined;
      if (this.result.min_length === "" || !this.result.allowed_other)
        this.result.min_length = undefined;
      if (this.result.max_length === "" || !this.result.allowed_other)
        this.result.max_length = undefined;
      if (this.result.min_selected === "")
        this.result.min_selected = undefined;
      if (this.result.max_selected === "")
        this.result.max_selected = undefined;
      this.$emit("update", this.result);
      if (this.result.default === undefined) {
      this.result.default = [];
    }
    },
  },
};
</script>

<style type="scss" scoped>
  .option-label {
    display: inline-block;
    width: 220px;
    white-space: nowrap;
    height: 20px;
  }
  .no-width {
    width: auto;
    margin-right: 5px;
  }
  .allowed_other:after {
    content: 'Permitir a resposta "outro"';
  }
</style>
