<template>
  <img
    :id="qid"
    :src="question.properties.content"
    alt=""
  >
</template>

<script>
export default {
  name: "ImageField",
  props: ["question", "questionIndex", "sectionIndex", "qid"],
  emits: ["update"],
  mounted() {
    this.update();
  },
  methods: {
    update() {
      this.$emit("update", {
        sectionIndex: this.sectionIndex,
        questionIndex: this.questionIndex,
        answer: this.answer,
      });
    },
  },
};
</script>
