import { createRouter, createWebHistory, RouteRecordRaw } from "vue-router";
import CreateResponse from "../views/CreateResponse.vue";
import ViewResponse from "../views/ViewResponse.vue";
import EditResponse from "../views/EditResponse.vue";
import ViewAllResponses from "../views/ViewAllResponses.vue";
import ViewForms from "../views/ViewForms.vue";
import CreateForm from "../views/CreateForm.vue";
import EditForm from "../views/EditForm.vue";
import FormsManagement from "../views/FormsManagement.vue";
import AvailableFormsManagement from "../views/AvailableFormsManagement.vue";

const routes: Array<RouteRecordRaw> = [
  {
    path: "",
    name: "ViewForms",
    component: ViewForms,
  },
  {
    path: "/respond/:id",
    name: "CreateResponse",
    component: CreateResponse,
  },
  {
    path: "/view_response/:id",
    name: "View Response",
    component: ViewResponse,
  },
  {
    path: "/edit_response/:id",
    name: "Edit Response",
    component: EditResponse,
  },
  {
    path: "/responses/:id",
    name: "View All Responses",
    component: ViewAllResponses,
  },
  {
    path: "/createForm",
    name: "Create Form",
    component: CreateForm,
  },
  {
    path: "/editForm/:id",
    name: "Edit Form",
    component: EditForm,
  },
  {
    path: "/formsManagement",
    name: "Manage Forms",
    component: FormsManagement,
  },
  {
    path: "/availableFormsManagement",
    name: "Manage Available Forms",
    component: AvailableFormsManagement,
  }
];

const router = createRouter({
  history: createWebHistory("/forms/"),
  routes,
});

export default router;
