<template>
  <div
    class="flex row"
    :class="invalid && (answer === undefined || answer == '') ? invalid : ''"
  >
    <input
      :id="qid"
      v-model="answer"
      type="datetime-local"
      :name="qid"
      :min="question.properties.begin"
      :max="question.properties.end"
      :required="this.question.required"
      @change="update"
      @invalid="invalid = 'invalid'"
    >
  </div>
</template>

<script>
export default {
  name: "DateField",
  props: ["question", "questionIndex", "sectionIndex", "qid", "value"],
  emits: ["update"],
  data() {
    return {
      answer:
        this.value !== undefined
          ? this.value
          : this.question.properties.default,
      invalid: false,
    };
  },
  mounted() {
    this.update();
  },
  methods: {
    update() {
      this.$emit("update", {
        sectionIndex: this.sectionIndex,
        questionIndex: this.questionIndex,
        answer: this.answer,
      });
    },
  },
};
</script>
