<template>
  <div
    class="flex row width-100"
    :class="
      invalid &&
        (((answer === undefined || answer == '') && question.required) ||
          (answer.length > 0 &&
            (answer.length < question.properties.min_length ||
              answer.length > question.properties.maxlength)))
        ? invalid
        : ''
    "
  >
    <span
      v-if="question.properties.max_length"
      class="flex row width-100 max-char"
    >Caracteres: {{ answer.length }}/{{
      question.properties.max_length
    }}</span>
    <textarea
      :id="qid"
      v-model="answer"
      class="flex row width-100"
      :name="qid"
      :rows="question.properties.default_lines"
      :minlength="question.properties.min_length"
      :maxlength="question.properties.max_length"
      :placeholder="question.properties.placeholder"
      @change="update"
      @invalid="invalid = 'invalid'"
    />
  </div>
</template>

<script>
export default {
  name: "LongTextField",
  props: ["question", "questionIndex", "sectionIndex", "qid", "value"],
  emits: ["update"],
  data() {
    return {
      answer:
        this.value !== undefined
          ? this.value
          : this.question.properties.default,
      invalid: false,
    };
  },
  mounted() {
    this.update();
  },
  methods: {
    update() {
      this.$emit("update", {
        sectionIndex: this.sectionIndex,
        questionIndex: this.questionIndex,
        answer: this.answer,
      });
    },
  },
};
</script>

<style scoped>
@media only screen and (min-width: 768px) {
  .max-char {
    margin-top: -24px;
    justify-content: flex-end;
  }
}
</style>
