<template>
  <div>
    <h1>{{ api_data.title }}</h1>
    <div class="flex column ca">
      <div
        v-for="(section, sectionIndex) in api_data.form.sections"
        :key="sectionIndex"
      >
        <hr>
        <h2>
          <span
            v-if="!api_data.form.hide_section_numbers"
          >{{ sectionIndex + 1 }}. </span>{{ section.section_name }}
        </h2>
        <div
          v-for="(question, questionIndex) in section.questions"
          :key="questionIndex"
          :set="(qid = 's' + sectionIndex + '_q' + questionIndex)"
          class="flex column ca vla no-bottom question"
        >
          <label
            class="question-field"
            :for="qid"
          ><h3>
            <span
              v-if="!api_data.form.hide_question_numbers && !section.hide_question_numbers"
            >{{ questionIndex + 1 }}. </span>{{ question.question }}
            <span
              v-if="question.required"
              class="requiredticker"
            >*</span>
          </h3></label>
          <div v-if="question.properties.field_type == 'selectboxes'">
            <span
              v-if="
                form_response.content.sections[sectionIndex][questionIndex]
                  .answer === undefined ||
                  form_response.content.sections[sectionIndex][questionIndex]
                    .answer == '' ||
                  form_response.content.sections[sectionIndex][questionIndex]
                    .answer == []
              "
              class="noanswer"
            >
              Sem resposta
            </span>
            <ul v-else>
              <li
                v-for="answer in form_response.content.sections[sectionIndex][questionIndex].answer"
                :key="answer"
              >
                <span v-if="answer === parseInt(answer, 10)">
                  {{ question.properties.options[answer] }}
                </span>
                <span v-else> Outro: {{ answer }} </span>
              </li>
            </ul>
          </div>
          <div
            v-else-if="question.properties.field_type == 'multiplechoice'"
            :set="(answer = form_response.content.sections[sectionIndex][questionIndex].answer)"
          >
            <span
              v-if="answer === undefined"
              class="noanswer"
            >
              Sem resposta
            </span>
            <span v-else-if="answer === parseInt(answer, 10)">
              {{ question.properties.options[answer] }}
            </span>
            <span v-else>
              {{ question.properties.other_string }}<span v-if="answer"> "{{ answer }}"</span>
            </span>
          </div>
          <div v-else-if="question.properties.field_type == 'date'">
            {{ new Date(form_response.content.sections[sectionIndex][questionIndex].answer).toLocaleDateString() }}
          </div>
          <div v-else-if="question.properties.field_type == 'datetime'">
            {{ new Date(form_response.content.sections[sectionIndex][questionIndex].answer).toLocaleString() }}
          </div>
          <div v-else>
            {{ form_response.content.sections[sectionIndex][questionIndex].answer }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as components from "@/fields";
import { API } from "@/http-common";
export default {
  name: "ViewResponse",
  components,
  data() {
    return {
      api_data: {},
      form_response: {},
    };
  },
  created() {
    API.get("formAnswer/".concat(this.$route.params.id))
      .then((response) => {
        this.initializeResponseData(response);
        API.get("form/".concat(response.data.form)).then((response) =>
          this.initializeData(response)).catch((error) =>
            console.log(error.response)
          );
      })
      .catch((error) => console.log(error.response));
  },
  methods: {
    initializeData(response) {
      this.api_data = response.data;
      for (var i = 0; i < this.api_data.form.sections.length; /*do nothing*/) {
        for (var j = 0; j < this.api_data.form.sections[i].questions.length; /* do nothing */) {
          if (this.api_data.form.sections[i].questions[j].properties.field_type.includes("_exposition")) {
            this.api_data.form.sections[i].questions.splice(j, 1);
          }
          else
            j++;
        }
        if (this.api_data.form.sections[i].questions.length > 0) {
          i++;
        }
        else {
          this.api_data.form.sections.splice(i, 1);
        }
      }
    },
    initializeResponseData(response) {
      this.form_response = response.data;
      for (var i = 0; i < this.form_response.content.sections.length; /*do nothing*/) {
        for (var j = 0; j < this.form_response.content.sections[i].length; /* do nothing */) {
          if (this.form_response.content.sections[i][j].field_type.includes("_exposition")) {
            this.form_response.content.sections[i].splice(j, 1);
          }
          else
            j++;
        }
        if (this.form_response.content.sections[i].length > 0) {
          i++;
        }
        else {
          this.form_response.content.sections.splice(i, 1);
        }
      }
    },
  },
};
</script>

<style lang="scss">
#form-area {
  width: 100%;
}
.no-bottom {
  padding-bottom: 0;
}

.invalid {
  background-color: #e99;
  padding: 5px;
}

.requiredticker {
  color: #b11;
}

.noanswer {
  color: #ccc;
}
</style>
