<template>
  <div
    class="flex row"
    :class="
      invalid &&
        (answer === undefined ||
          answer == '' ||
          answer < question.properties.min ||
          answer > question.properties.max)
        ? invalid
        : ''
    "
  >
    <span
      v-if="question.properties.style == 'slider'"
      class="flex row"
      style="margin-right: 20px"
    >
      {{ question.properties.prefix }}
      <input
        :id="qid"
        v-model="answer"
        type="range"
        class="spaced"
        :name="qid"
        :min="question.properties.min"
        :max="question.properties.max"
        :step="Math.pow(10, -question.properties.decimal_places)"
        :required="question.required"
        @change="update"
        @invalid="invalid = 'invalid'"
      >
      {{ question.properties.suffix }}
    </span>
    <span class="flex row">
      <input
        :id="question.properties.style == 'slider' ? qid + '_alt' : qid"
        v-model="answer"
        type="number"
        :name="qid"
        :min="question.properties.min"
        :max="question.properties.max"
        :step="Math.pow(10, -question.properties.decimal_places)"
        :required="question.required"
        @change="update"
        @invalid="invalid = 'invalid'"
      >
    </span>
  </div>
</template>

<script>
export default {
  name: "NumberField",
  props: ["question", "questionIndex", "sectionIndex", "qid", "value"],
  emits: ["update"],
  data() {
    return {
      answer:
        this.value !== undefined
          ? this.value
          : this.question.properties.default,
      invalid: false,
    };
  },
  mounted() {
    this.update();
  },
  methods: {
    update() {
      this.$emit("update", {
        sectionIndex: this.sectionIndex,
        questionIndex: this.questionIndex,
        answer: this.answer !== undefined ? Number(this.answer) : this.answer,
      });
    },
  },
};
</script>

<style scoped>
span.textbox {
  background-color: #fff;
  color: #888;
  line-height: 20px;
  height: 20px;
  padding: 0px 5px 0px 5px;
  border: 1px #888 solid;
  font-size: 9pt;
}
span.textbox input {
  border: 0px;
  background-color: #fff;
  text-align: right;
}

textarea:focus,
input:focus {
  outline: none;
}
</style>
