<template>
  <div
    :id="qid"
    class="flex row"
  >
    {{ question.properties.content }}
  </div>
</template>

<script>
export default {
  name: "DescriptionField",
  props: ["question", "questionIndex", "sectionIndex", "qid"],
  emits: ["update"],
  mounted() {
    this.update();
  },
  methods: {
    update() {
      this.$emit("update", {
        sectionIndex: this.sectionIndex,
        questionIndex: this.questionIndex,
      });
    },
  },
};
</script>
