<template>
  <div id="responses" v-if="forms !== undefined">
    <h1>Formulários da Plataforma</h1>
    <table class="">
      <caption>
        Formulários
      </caption>
      <thead>
        <tr>
          <th>
            Formulário
          </th>
          <th>Respostas</th>
          <th>Editar</th>
          <th>Eliminar</th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="form in forms"
          :key="form.id"
        >
          <td>
              {{ form.title }}
          </td>
          <td align="right">
              {{ form.responses }}
          </td>
          <td align="right">
              <a :href="'editForm/' + form.id">Editar</a>
          </td>
          <td align="right">
              <a href="#" @click="remove(form.id)">Eliminar</a>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import * as components from "@/fields";
import { API } from "@/http-common";
export default {
  name: "CreateResponse",
  components,
  data() {
    return {
      forms: undefined
    };
  },
  created() {
    API.get("form/")
      .then((response) => this.forms = response.data)
      .catch((error) => console.log(error.response));
  },
  methods: {
    remove(id) {
      if (confirm('De certeza que quer eliminar este formulário?')) {
        const options = {
          method: "delete",
          url: "/form/" + id + "/",
          headers: {
            "X-CSRFToken": this.getCookie("csrftoken"),
          },
          transformResponse: [
            (data) => {
              this.forms = this.forms.filter((x) => (x.id != id));
              return data; 
            },
          ],
        };
        API(options);
      }
    },
    getCookie(name) {
      let cookieValue = null;
      if (document.cookie && document.cookie !== "") {
        const cookies = document.cookie.split(";");
        for (let i = 0; i < cookies.length; i++) {
          const cookie = cookies[i].trim();
          // Does this cookie string begin with the name we want?
          if (cookie.substring(0, name.length + 1) === name + "=") {
            cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
            break;
          }
        }
      }
      return cookieValue;
    },
  },
};
</script>

<style lang="scss">
#form-area {
  width: 100%;
}
.no-bottom {
  padding-bottom: 0;
}

.invalid {
  background-color: #e99;
  padding: 5px;
}

.requiredticker {
  color: #b11;
}
</style>
