<template>
  <div
    class="flex row"
    :class="
      invalid &&
        (answer === undefined ||
          answer == '')
        ? invalid
        : ''
    "
  >
    <input
      v-if="!valid"
      :id="qid + '_validator'"
      :key="valid"
      value="O máximo deve ser superior ao mínimo."
      type="text"
      oninvalid="setCustomValidity('O máximo deve ser superior ao mínimo.')"
      :pattern="valid ? undefined : '.a'"
      class="warning-field removable"
      onkeydown="return false;"
      :size="'O máximo deve ser superior ao mínimo.'.length-6"
      @invalid="invalid = 'invalid'"
    >
    <div
      id="advanced_options_control"
      class="flex row unselectable"
      @click="advancedOptionsShown = !advancedOptionsShown"
    >
      <h2>Opções avançadas</h2>
      <i
        v-show="!advancedOptionsShown"
        class="arrow right"
      /><i
        v-show="advancedOptionsShown"
        class="arrow down"
      />
    </div>
    <div v-show="advancedOptionsShown">
      <label
        class="option-label"
        :for="qid+'_default'"
      >Valor por defeito: </label>
      <input
        :id="qid+'_default'"
        v-model.number="this.result.default"
        type="number"
        @change="update"
      >
      <br>
      <label
        class="option-label"
        :for="qid+'_placeholder'"
      >Texto de sugestão: </label>
      <input
        :id="qid+'_placeholder'"
        v-model="this.result.placeholder"
        type="text"
        @change="update"
      >
      <br>
      <label
        class="option-label"
        :for="qid+'_min'"
      >Mínimo: </label>
      <input
        :id="qid+'_min'"
        v-model.number="this.result.min"
        type="number"
        step="1"
        min="1"
        @change="update"
      >
      <br>
      <label
        class="option-label"
        :for="qid+'_max'"
      >Máximo: </label>
      <input
        :id="qid+'_max'"
        v-model.number="this.result.max"
        type="number"
        step="1"
        min="1"
        @change="update"
      >
      <br>
      <label
        class="option-label"
        :for="qid+'_decimal_places'"
      >Casas decimais: </label>
      <input
        :id="qid+'_decimal_places'"
        v-model.number="this.result.decimal_places"
        type="number"
        step="1"
        min="0"
        @change="update"
      >
      <br>
      <label
        class="option-label"
        :for="qid+'_prefix'"
      >Prefixo: </label>
      <input
        :id="qid+'_prefix'"
        v-model="this.result.prefix"
        type="text"
        @change="update"
      >
      <br>
      <label
        class="option-label"
        :for="qid+'_suffix'"
      >Sufixo: </label>
      <input
        :id="qid+'_suffix'"
        v-model="this.result.suffix"
        type="text"
        @change="update"
      >
      <br>
      <label
        class="option-label"
        :for="qid+'_number'"
      >Estilo: </label>
      <select
        :id="qid+'_number'"
        v-model="this.result.style"
        required
        @change="update"
      >
        <option value="text">
          Texto
        </option>
        <option value="slider">
          Barra deslizante
        </option>
      </select>
    </div>
  </div>
</template>

<script>
export default {
  name: "NumberField",
  props: ["qid", "properties"],
  emits: ["update"],
  data() {
    return {
      result: this.properties,
      valid: true,
      invalid: "",
      advancedOptionsShown: false,
    };
  },
  created() {
    if (this.result.style === undefined) {
      this.result.style = "text";
      this.update();
    }
  },
  methods: {
    update() {
      this.valid = !this.result.min || !this.result.max || this.result.min <= this.result.max
      if (this.result.default === "")
        this.result.default = undefined;
      if (this.result.placeholder === "")
        this.result.placeholder = undefined;
      if (this.result.min === "")
        this.result.min = undefined;
      if (this.result.max === "")
        this.result.max = undefined;
      if (this.result.decimal_places === "")
        this.result.decimal_places = undefined;
      if (this.result.prefix === "")
        this.result.prefix = undefined;
      if (this.result.suffix === "")
        this.result.suffix = undefined;
      this.$emit("update", this.result);
    },
  },
};
</script>

<style type="scss">
  .option-label {
    display: inline-block;
    width: 145px;
    white-space: nowrap;
  }
</style>
