<template>
  <div
    v-if="api_data"
    id="responses"
  >
    <h1>
      Respostas<template v-if="user">
        de {{ user }}
      </template><template v-if="event">
        ao evento {{ event }}
      </template>
    </h1>
    <table>
      <caption style="background-color: #365c98;">
        {{ api_data.title }}
      </caption>
      <thead>
        <tr class="sections">
          <th
            style="background-color: #3d6cb8"
            rowspan="3"
          >
            Autor da Resposta
          </th>
          <th
            v-if="!event"
            style="background-color: #3d6cb8"
            rowspan="3"
          >
            Evento
          </th>
          <th
            style="background-color: #3d6cb8"
            rowspan="3"
          >
            Estado
          </th>
          <th
            v-for="(section, sectionIndex) in api_data.form.sections"
            :key="sectionIndex"
            :colspan="displayed_sections[sectionIndex] == true ? section.questions.length : 1"
            :rowspan="displayed_sections[sectionIndex] == true ? 1 : 2"
            style="background-color: #3d6cb8"
          >
            {{ section.section_name }}
          </th>
        </tr>
        <tr>
          <div
            v-for="(section, sectionIndex) in api_data.form.sections" 
            v-show="displayed_sections[sectionIndex] == true"
            :key="sectionIndex"
            style="display: contents;"
          >
            <th
              v-for="(question, questionIndex) in section.questions"
              :key="questionIndex"
            >
              {{ question.question }}
            </th>
          </div>
        </tr>
        <tr>
          <th
            v-for="(section, sectionIndex) in api_data.form.sections"
            :key="sectionIndex"
            :class="displayed_sections[sectionIndex] ? 'view_section' : 'hide_section'"
            :colspan="displayed_sections[sectionIndex] ? section.questions.length : 1"
            @click="displayed_sections[sectionIndex] = !displayed_sections[sectionIndex];"
          >
            {{ displayed_sections[sectionIndex] ? 'Esconder secção' : 'Ver secção' }}
          </th>
        </tr>
        <tr>
          <th>
            <input
              v-model="authorSearch"
              class="searchBar"
              type="text"
            >
          </th>
          <th v-if="!event">
            <input
              v-model="eventSearch"
              class="searchBar"
              type="text"
            >
          </th>
          <th>
            <input
              v-model="statusSearch"
              class="searchBar"
              type="text"
            >
          </th>
          <div
            v-for="(section, sectionIndex) in api_data.form.sections"
            :key="sectionIndex"
            style="display: contents;"
          >
            <div
              v-show="displayed_sections[sectionIndex]"
              style="display: contents;"
            >
              <th
                v-for="(question, questionIndex) in section.questions"
                :key="questionIndex"
              >
                <input
                  v-model="question.filter"
                  class="searchBar"
                  type="text"
                  @changed="filter"
                >
              </th>
            </div>
            <td
              v-show="displayed_sections[sectionIndex] == false"
              class="fillerCell"
            />
          </div>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="(response, responseIndex) in filteredResponses()"
          :key="responseIndex"
        >
          <td class="author">
            <span
              v-if="response.author"
              :href="'/profile/'+response.answerer"
            >{{ response.author }}</span>
            <template v-else>
              Anónimo
            </template><span
              class="mouseover"
              :title="new Date(response.created_at).toLocaleString()"
            >, há {{ timeSince(response.created_at) }}</span><span
              v-if="response.created_at != response.updated_at"
              :class="response.created_at != response.updated_at ? 'edited' : ''"
              class="mouseover"
              :title="'Editado por ' + (!response.editor ? 'Anónimo' : response.editor) + ' há ' + timeSince(response.updated_at) + ' (' + new Date(response.updated_at).toLocaleString() + ')'"
            >*</span><br>
          </td>
          <td v-if="!event">
            {{ response.eventName }}
          </td>
          <td>
            <select
              v-model="answerStatuses[response.id]"
              class="status"
              :style="{backgroundColor: statuses[answerStatuses[response.id]].color}"
              @change="changeStatus(response.id, answerStatuses[response.id])"
            >
              <option
                v-for="(status, key) in statuses"
                :key="key"
                :value="key"
                style="background-color: white; color: black;"
              >
                {{ status.name }}
              </option>
            </select>
          </td>
          <div
            v-for="(section, sectionIndex) in response.content.sections"
            :key="sectionIndex"
            style="display: contents;"
          >
            <div
              v-show="displayed_sections[sectionIndex]"
              style="display:contents;"
            >
              <td
                v-for="(answer, answerIndex) in section"
            
                :key="answerIndex"
                :style="answer.answer === undefined ? 'background-color: rgba(0, 0, 0, 0.25);' : ''"
              >
                <template v-if="answer.answer !== undefined">
                  <template v-if="Object.prototype.toString.call( answer.answer ) === '[object Array]'">
                    <li
                      v-for="(option, optionIndex) in answer.answer"
                      :key="optionIndex"
                    >
                      {{ option }}
                    </li>
                  </template>
                  <template v-else>
                    {{ answer.answer }}
                  </template>
                </template>
              </td>
            </div>
            <td
              v-show="displayed_sections[sectionIndex] == false"
              class="fillerCell"
            />
          </div>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import * as components from "@/fields";
import { API } from "@/http-common";
export default {
  name: "ViewAllResponses",
  components,
  data() {
    return {
      api_data: undefined,
      form_responses: [],
      displayed_sections: [],
      event: undefined,
      user: undefined,
      statuses: {},
      authorSearch: "",
      eventSearch: "",
      statusSearch: "",
      answerStatuses: [],
      interval : undefined,
    };
  },
  created() {
    API.get("form/".concat(this.$route.params.id))
      .then((response) => 
        this.initializeData(response)
      )
      .catch((error) => console.log(error.response));
    var queryString = "formAnswer/?form=".concat(this.$route.params.id);
    if (this.$route.query.event !== undefined) {
      queryString = queryString.concat("&event=").concat(this.$route.query.event)
      API.get("event/".concat(this.$route.query.event))
       .then(response => this.event = response.data.name)
       .catch((error) => console.log(error.response));
    }
    if (this.$route.query.user !== undefined) {
      queryString = queryString.concat("&user=").concat(this.$route.query.user)
      API.get("user/".concat(this.$route.query.user))
       .then(response => this.user = response.data.username);
    }
    API.get(queryString)
      .then((response) => 
        this.initializeAnswers(response)
      );

    API.get("answerStatus/")
      .then((response) => {
        this.statuses = response.data;
      });
    this.getStatuses();
    //this.interval = setInterval(() => this.getStatuses(), 10000);
  },
  methods: {
    getStatuses() {
      API.get("formAnswerStatus/")
      .then((response) => {
        this.answerStatuses = response.data;
      });
    },
    changeStatus(answer, status) {
      const options = {
        method: "put",
        url: "/formAnswerStatus/" + answer + "/",
        headers: {
          "X-CSRFToken": this.getCookie("csrftoken"),
        },
        data: {
          "status": status
        },
        transformResponse: [
          (data) => {
            this.response = data;
            return data;
          },
        ],
      };
      API(options);
    },
    initializeData(response) {
      this.api_data = response.data;
      for (var i = 0; i < this.api_data.form.sections.length; /*do nothing*/) {
        for (var j = 0; j < this.api_data.form.sections[i].questions.length; /* do nothing */) {
          if (this.api_data.form.sections[i].questions[j].properties.field_type.includes("_exposition")) {
            this.api_data.form.sections[i].questions.splice(j, 1);
          }
          else
            j++;
        }
        if (this.api_data.form.sections[i].questions.length > 0) {
          this.displayed_sections[i] = true;
          i++;
        }
        else {
          this.api_data.form.sections.splice(i, 1);
        }
      }
    },
    initializeAnswers(response) {
      this.form_responses = response.data;
      for (var replyIndex = 0; replyIndex < this.form_responses.length; replyIndex++) {
        const reply = this.form_responses[replyIndex];
        for (var sectionIndex = 0; sectionIndex < reply.content.sections.length; sectionIndex++) {
          const section = reply.content.sections[sectionIndex];
          for (var answerIndex = 0; answerIndex < section.length; answerIndex++) {
            const answer = section[answerIndex];
            if (answer.answer !== undefined) {
              if (answer.field_type=='selectboxes') {
                for (var optionIndex = 0; optionIndex < answer.answer.length; optionIndex++) {
                  const option = answer.answer[optionIndex];
                  if (option === parseInt(option, 10))
                    answer.answer[optionIndex] = this.api_data.form.sections[sectionIndex].questions[answerIndex].properties.options[option];
                  else
                    answer.answer = "Outro: \"" + answer.answer + "\""
                }
              }
              else if (answer.field_type=='multiplechoice') {
                if (answer.answer === parseInt(answer.answer, 10))
                  answer.answer = this.api_data.form.sections[sectionIndex].questions[answerIndex].properties.options[answer.answer];
                else
                  answer.answer = "Outro: \"" + answer.answer + "\""
              }
              else if (answer.field_type=='date')
                answer.answer = new Date(answer.answer).toLocaleDateString();
              else if (answer.field_type=='datetime')
                answer.answer = new Date(answer.answer).toLocaleString();
            }
          }
        }
      }
    },
    allFalse() {
      for (var i = 0; i < this.displayed_sections.length; i++)
        if (this.displayed_sections[i] == true)
          return false;
      return true;
    },
    stringIn(a, b) {
      return (
          a
            .toLowerCase()
            .normalize("NFD")
            .replace(/[\u0300-\u036f]/g, "")
            .indexOf(
              b
                .toLowerCase()
                .normalize("NFD")
                .replace(/[\u0300-\u036f]/g, "")
            ) != -1
        );
    },
    filteredResponses() {
      var result = this.form_responses;
      if (this.authorSearch != "")
        result = result.filter(x => this.stringIn(x.author === undefined ? "Anónimo" : x.author, this.authorSearch));
      if (this.eventSearch != "")
        result = result.filter(x => {return x.eventName ? this.stringIn(x.eventName, this.eventSearch) : false});
      if (this.statusSearch != "")
        result = result.filter(x => this.stringIn(this.statuses[this.answerStatuses[x.id]].name, this.statusSearch));
      for (var sectionIndex = 0; sectionIndex < this.api_data.form.sections.length; sectionIndex++) {
        if (this.displayed_sections[sectionIndex]) {
          var section = this.api_data.form.sections[sectionIndex];
          for (var questionIndex = 0; questionIndex < section.questions.length; questionIndex++) {
            var question = section.questions[questionIndex]
            if (question.filter && question.filter != "") {
              result = result.filter(x => this.stringIn(String(x.content.sections[sectionIndex][questionIndex].answer), question.filter));
            }
          }
        }
      }
      return result;
    },
    timeSince(date) {
      if (typeof date !== 'object') {
        date = new Date(date);
      }

      var seconds = Math.floor((new Date() - date) / 1000);
      var intervalType;

      var interval = Math.floor(seconds / 31536000);
      if (interval >= 1) {
        intervalType = 'ano';
      } else {
        interval = Math.floor(seconds / 2592000);
        if (interval >= 1) {
          intervalType = 'mês';
        } else {
          interval = Math.floor(seconds / 86400);
          if (interval >= 1) {
            intervalType = 'dia';
          } else {
            interval = Math.floor(seconds / 3600);
            if (interval >= 1) {
              intervalType = "hora";
            } else {
              interval = Math.floor(seconds / 60);
              if (interval >= 1) {
                intervalType = "minuto";
              } else {
                interval = seconds;
                intervalType = "segundo";
              }
            }
          }
        }
      }

      if (interval > 1 || interval === 0) {
        if (intervalType == "mês")
          intervalType = "meses"
        else
          intervalType += 's';
      }

      return interval + ' ' + intervalType;
    },
    getCookie(name) {
      let cookieValue = null;
      if (document.cookie && document.cookie !== "") {
        const cookies = document.cookie.split(";");
        for (let i = 0; i < cookies.length; i++) {
          const cookie = cookies[i].trim();
          // Does this cookie string begin with the name we want?
          if (cookie.substring(0, name.length + 1) === name + "=") {
            cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
            break;
          }
        }
      }
      return cookieValue;
    },
  }
};
</script>

<style lang="scss">
#responses {
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 100%;
  overflow-x: auto;

  table {
    table-layout:fixed;
  }

  td {
    overflow-x: auto;
  }

  td, th , caption {
    border: 1px solid #ddd;
    padding: 8px;
  }

  tr:nth-child(even){background-color: #f2f2f2;}

  tr:hover {background-color: #ddd;}

  th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #3273dc;
    color: white;
    font-weight: normal;
    white-space: nowrap;
    vertical-align:middle;
  }

  caption {
    padding-top: 12px;
    padding-bottom: 12px;
    background-color: #3273dc;
    color: white;
    font-weight: bolder;
  }

  .sections th {
    font-weight: bold;
    text-align: center;
  }

  .fillerCell {
    background-color: #ccc;
    border-color: #ccc;
  }

  .view_section {
    background-color: #7ea7e9;
    text-align: center;
    cursor: pointer;
    border-radius: 10px;
    &:hover {background-color: #6586ba;}
  }

  .hide_section {
    background-color: #7498d5;
    text-align: center;
    cursor: pointer;
    border-radius: 10px;
    &:hover {background-color: #6586ba;}
  }

  .author {
    white-space: nowrap;
  }

  .mouseover {
    cursor: pointer;
  }

  .edited {
    color: blue;
    font-weight: bolder;
    font-size: 1.1em;
  }
  .status {
    padding: 5px;
    border-radius: 10px;
    color: white;
    cursor: pointer;
  }

  .searchBar {
    border-radius: 5px;
    width: 100%;
    height: 100%;
  }
}
</style>
