<template>
  <div
    class="flex row"
    :class="
      invalid &&
        (answer === undefined ||
          answer == '')
        ? invalid
        : ''
    "
  >
    <input
      v-if="!valid"
      :id="qid + '_validator'"
      :key="valid"
      value="O tamanho máximo deve ser superior ao tamanho mínimo."
      type="text"
      oninvalid="setCustomValidity('O tamanho máximo deve ser superior ao tamanho mínimo.')"
      :pattern="valid ? undefined : '.a'"
      class="warning-field removable"
      onkeydown="return false;"
      :size="'O tamanho máximo deve ser superior ao tamanho mínimo.'.length-6"
      @invalid="invalid = 'invalid'"
    >
    <div
      id="advanced_options_control"
      class="flex row unselectable"
      @click="advancedOptionsShown = !advancedOptionsShown"
    >
      <h2>Opções avançadas</h2>
      <i
        v-show="!advancedOptionsShown"
        class="arrow right"
      /><i
        v-show="advancedOptionsShown"
        class="arrow down"
      />
    </div>
    <div v-show="advancedOptionsShown">
      <label
        class="option-label"
        :for="qid+'_default'"
      >Valor por defeito: </label>
      <input
        :id="qid+'_default'"
        v-model="this.result.default"
        type="text"
        @change="update"
      >
      <br>
      <label
        class="option-label"
        :for="qid+'_placeholder'"
      >Texto de sugestão: </label>
      <input
        :id="qid+'_placeholder'"
        v-model="this.result.placeholder"
        type="text"
        @change="update"
      >
      <br>
      <label
        class="option-label"
        :for="qid+'_min_length'"
      >Tamanho mínimo: </label>
      <input
        :id="qid+'_min_length'"
        v-model.number="this.result.min_length"
        type="number"
        step="1"
        min="1"
        @change="update"
      >
      <br>
      <label
        class="option-label"
        :for="qid+'_max_length'"
      >Tamanho máximo: </label>
      <input
        :id="qid+'_max_length'"
        v-model.number="this.result.max_length"
        type="number"
        step="1"
        min="1"
        @change="update"
      >
      <br>
      <label
        class="option-label"
        :for="qid+'_default_lines'"
      >Linhas de escrita: </label>
      <input
        :id="qid+'_default_lines'"
        v-model.number="this.result.default_lines"
        type="number"
        step="1"
        min="1"
        @change="update"
      >
    </div>
  </div>
</template>

<script>
export default {
  name: "LongTextField",
  props: ["qid", "properties"],
  emits: ["update"],
  data() {
    return {
      result: this.properties,
      valid: true,
      invalid: "",
      advancedOptionsShown: false,
    };
  },
  methods: {
    update() {
      this.valid = !this.result.min_length || !this.result.max_length || this.result.min_length <= this.result.max_length
      if (this.result.default === "")
        this.result.default = undefined;
      if (this.result.placeholder === "")
        this.result.placeholder = undefined;
      if (this.result.min_length === "")
        this.result.min_length = undefined;
      if (this.result.max_length === "")
        this.result.max_length = undefined;
      if (this.result.default_lines === "")
        this.result.default_lines = undefined;
      this.$emit("update", this.result);
    },
  },
};
</script>

<style type="scss">
  .option-label {
    display: inline-block;
    width: 145px;
    white-space: nowrap;
  }
</style>
