<template>
  <div
    class="flex row"
    :class="
      invalid &&
        (answer === undefined ||
          answer == '' ||
          answer.length < question.properties.min_length ||
          answer.length > question.properties.maxlength)
        ? invalid
        : ''
    "
  >
    <!-- <input type="password" :id="qid" :name="qid" v-if="question.properties.sensitive" v-model="answer" :placeholder="question.properties.placeholder" :minlength="question.properties.min_length" :maxlength="question.properties.max_length" @change="update" :required="question.required" @invalid="invalid = 'invalid'"> -->
    <input
      :id="qid"
      v-model="answer"
      type="text"
      :name="qid"
      :placeholder="question.properties.placeholder"
      :minlength="question.properties.min_length"
      :maxlength="question.properties.max_length"
      :required="question.required"
      @change="update"
      @invalid="invalid = 'invalid'"
    >
  </div>
</template>

<script>
export default {
  name: "ShortTextField",
  props: ["question", "questionIndex", "sectionIndex", "qid", "value"],
  emits: ["update"],
  data() {
    return {
      answer:
        this.value !== undefined
          ? this.value
          : this.question.properties.default,
      invalid: "",
    };
  },
  mounted() {
    this.update();
  },
  methods: {
    update() {
      this.$emit("update", {
        sectionIndex: this.sectionIndex,
        questionIndex: this.questionIndex,
        answer: this.answer,
      });
    },
  },
};
</script>
