<template>
  <div :class="invalid && (valid === undefined || valid == '') ? invalid : ''">
    <div v-if="question.properties.style == 'radio'">
      <input
        :key="valid"
        v-model="valid"
        type="text"
        oninvalid="setCustomValidity('Isto é um campo obrigatório')"
        required
        onkeydown="return false;"
        style="
          caret-color: transparent !important;
          cursor: default;
          opacity: 0;
          width: 0px;
          height: 0px;
        "
        :name="qid + '_validator'"
        @invalid="invalid = 'invalid'"
      >
      <div
        v-for="(option, index) in question.properties.options"
        :key="index"
        class="flex row vca"
      >
        <input
          :id="qid + '_o' + index"
          v-model="answer"
          type="radio"
          :name="qid"
          :value="index"
          @change="update"
        >
        <label
          class="spaced"
          :for="qid + '_o' + index"
        >{{ option }}</label><br>
      </div>
      <div
        v-if="question.properties.allowed_other"
        class="flex row vca"
        :class="
          invalid &&
            answer &&
            answer != parseInt(answer) &&
            (other_value.length < question.properties.min_length ||
              other_value.length > question.properties.max_length)
            ? invalid
            : ''
        "
      >
        <input
          :id="qid + '_other'"
          v-model="answer"
          type="radio"
          :name="qid"
          value="other"
          @change="update"
        >
        <label
          class="spaced"
          :for="qid + '_other'"
        >{{
          question.properties.other_string
        }}</label>
        <input
          v-if="
            question.properties.max_length != 0 &&
              answer &&
              answer != parseInt(answer)
          "
          v-model="other_value"
          type="text"
          :placeholder="question.properties.placeholder"
          :minlength="question.properties.min_length"
          :maxlength="question.properties.max_length"
          :required="question.properties.min_length"
          @change="update"
          @invalid="invalid = 'invalid'"
        >
      </div>
      <button
        v-if="!question.required"
        type="button"
        @click="clearSelection"
      >
        Limpar Seleção
      </button>
    </div>
    <div v-else>
      <select
        :id="qid"
        v-model="answer"
        :name="qid"
        :required="question.required"
        @invalid="invalid = 'invalid'"
        @change="update"
      >
        <option value="" />
        <option
          v-for="(option, index) in question.properties.options"
          :id="qid + '_o' + index"
          :key="index"
          :value="index"
          :selected="question.properties.default == index"
        >
          {{ option }}
        </option>
      </select>
    </div>
  </div>
</template>

<script>
export default {
  name: "MultipleChoiceField",
  props: ["question", "questionIndex", "sectionIndex", "qid", "value"],
  emits: ["update"],
  data() {
    return {
      answer:
        this.value !== undefined
          ? this.value != parseInt(this.value, 10)
            ? "other"
            : this.value
          : this.question.properties.default,
      other_value: this.value,
      valid: "",
      translation: {
        required: "Isto é um campo obrigatório",
      },
      invalid: "",
    };
  },
  mounted() {
    this.update();
  },
  methods: {
    clearSelection() {
      this.answer = undefined;
      this.update();
    },
    update() {
      if (typeof this.answer === "undefined" && this.question.required) {
        this.valid = "";
      } else {
        this.valid = "VALID";
      }
      this.$emit("update", {
        sectionIndex: this.sectionIndex,
        questionIndex: this.questionIndex,
        answer: this.answer == "other" ? this.other_value : this.answer,
      });
    },
  },
};
</script>
