<template>
  <div
    class="flex row"
    :class="
      invalid &&
        (answer === undefined ||
          answer == '')
        ? invalid
        : ''
    "
  >
    <textarea
      :id="qid"
      v-model="answer"
      type="text"
      :name="qid"
      required
      placeholder="Descrição"
      @change="update"
      @invalid="invalid = 'invalid'"
    />
  </div>
</template>

<script>
export default {
  name: "DescriptionField",
  props: ["question", "qid", "value", "properties"],
  emits: ["update"],
  data() {
    return {
      answer: this.properties.content,
      invalid: "",
    };
  },
  methods: {
    update() {
      this.$emit("update", {
        field_type: "text_exposition",
        content: this.answer,
      });
    },
  },
};
</script>
