<template>
  <div v-if="api_data">
    <h1>{{ api_data.title }}</h1>
    <div class="flex row ca">
      <form
        id="form-area"
        class="flex column"
        @submit.prevent="submit"
      >
        <div
          v-for="(section, sectionIndex) in api_data.form.sections"
          :key="sectionIndex"
        >
          <hr>
          <h2>
            <span
              v-if="!api_data.form.hide_section_numbers"
            >{{ sectionIndex + 1 }}. </span>{{ section.section_name }}
          </h2>
          <div
            v-for="(question, questionIndex) in section.questions"
            :key="questionIndex"
            :set="(qid = 's' + sectionIndex + '_q' + questionIndex)"
            class="flex column ca vla no-bottom question"
          >
            <label
              class="question-field"
              :for="qid"
            ><h3>
              <span
                v-if="
                  !api_data.form.hide_question_numbers &&
                    !section.hide_question_numbers
                "
              >{{ questionIndex + 1 }}. </span>{{ question.question
              }}<span
                v-if="question.required"
                class="requiredticker"
              >*</span>
            </h3></label>
            <component
              :is="question.properties.field_type"
              class="flex column ca vla"
              :question="question"
              :question-index="questionIndex"
              :section-index="sectionIndex"
              :qid="qid"
              :value="
                form_response.data.content.sections[sectionIndex][questionIndex]
                  .answer
              "
              @update="updateAnswer"
            />
          </div>
        </div>
        <div
          v-if="response === undefined"
          class="flex row ca"
          style="height: 70px;"
        >
          <a
            class="add_field add_section hoverable cancel"
            href="/forms"
          >
            Cancelar
          </a>
          <button
            type="submit"
            class="add_field add_section hoverable submit"
          >
            Submeter
          </button>
        </div>
        <template v-else>
          <h3
            v-if="response.id !== undefined"
            class="flex row ca"
            style="height: 70px;"
          >
            Formulário editado com sucesso. Será redirecionado em {{ api_data.redirect_delay }} segundos.
          </h3>
          <div v-else>
            Houve um erro interno na edição do formulário.<br>
            <textarea v-model="response" />
          </div>
        </template>
      </form>
    </div>
  </div>
</template>

<script>
import * as components from "@/fields";
import { API } from "@/http-common";
export default {
  name: "EditResponse",
  components,
  data() {
    return {
      api_data: undefined,
      form_response: {},
      answers: [],
      validation: [],
      response: undefined,
    };
  },
  created() {
    API.get("formAnswer/".concat(this.$route.params.id))
      .then((response) => {
        this.form_response = response;
        API.get("form/".concat(this.form_response.data.form)).then((response) =>
          this.initializeData(response)
        );
      });
  },
  methods: {
    submit() {
      if (confirm('Confirma que a informação introduzida está correta?')) {
        const options = {
          method: "put",
          url: "/formAnswer/" + this.$route.params.id + "/",
          headers: {
            "X-CSRFToken": this.getCookie("csrftoken"),
          },
          data: {
            content: { sections: this.answers },
            form: this.api_data.id,
          },
          transformResponse: [
            async (data) => {
              try {
                this.response = JSON.parse(data);
              } catch (e) {this.response = data;}
              if (this.response.id !== undefined) {
                await new Promise(r => setTimeout(r, this.api_data.redirect_delay*1000));
                window.location.href = this.api_data.redirect;
              }
              return data;
            },
          ],
        };
        API(options);
      }
    },
    updateAnswer(value) {
      this.$nextTick(function () {
        this.answers[value.sectionIndex][value.questionIndex]["answer"] =
          value["answer"];
        this.validation[value.sectionIndex][value.questionIndex] =
          value["valid"];
      });
    },
    initializeData(response) {
      this.api_data = response.data;
      for (var section in this.api_data.form.sections) {
        this.answers[section] = [];
        this.validation[section] = [];
        for (var question in this.api_data.form.sections[section].questions) {
          this.validation[section][question] = null;
          this.answers[section][question] = {
            field_type:
              this.api_data.form.sections[section].questions[question]
                .properties["field_type"],
          };
        }
      }
    },
    getCookie(name) {
      let cookieValue = null;
      if (document.cookie && document.cookie !== "") {
        const cookies = document.cookie.split(";");
        for (let i = 0; i < cookies.length; i++) {
          const cookie = cookies[i].trim();
          // Does this cookie string begin with the name we want?
          if (cookie.substring(0, name.length + 1) === name + "=") {
            cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
            break;
          }
        }
      }
      return cookieValue;
    },
  },
};
</script>

<style lang="scss">
#form-area {
  width: 100%;
}
.no-bottom {
  padding-bottom: 0;
}

.invalid {
  background-color: #e99;
  padding: 5px;
}

.requiredticker {
  color: #b11;
}
</style>
