<template>
  <div
    :key="invalid"
    class="flex row"
    :class="
      invalid && (!answer || answer.length != 11) ? invalid
      : ''"
  >
    <label :for="qid">Insira o ID do vídeo (por exemplo "dQw4w9WgXcQ")</label>
    <input
      :id="qid"
      v-model="answer"
      type="text"
      :name="qid"
      
      minlength="11"
      maxlength="11"
      required
      @change="update"
      @invalid="invalid = 'invalid'"
    >
  </div>
</template>

<script>
export default {
  name: "YouTubeField",
  props: ["question", "qid", "value", "properties"],
  emits: ["update"],
  data() {
    return {
      answer: this.properties.content,
      invalid: ""
    };
  },
  methods: {
    update() {
      if (this.answer && this.answer.length == 11) {
        this.$emit("update", {
          field_type: "youtube_exposition",
          content: this.answer,
        });
      }
    },
  },
};
</script>
