<template>
  <div
    class="flex row"
    :class="
      invalid &&
        (answer === undefined ||
          answer == '')
        ? invalid
        : ''
    "
  >
    <input
      v-if="!valid"
      :id="qid + '_validator'"
      :key="valid"
      value="A data e hora final deve ser após a data inicial."
      type="text"
      oninvalid="setCustomValidity('A data e hora final deve ser após a data inicial.')"
      :pattern="valid ? undefined : '.a'"
      class="warning-field removable"
      onkeydown="return false;"
      :size="'A data e hora final deve ser após a data inicial.'.length-6"
      @invalid="invalid = 'invalid'"
    >
    <div
      id="advanced_options_control"
      class="flex row unselectable"
      @click="advancedOptionsShown = !advancedOptionsShown"
    >
      <h2>Opções avançadas</h2>
      <i
        v-show="!advancedOptionsShown"
        class="arrow right"
      /><i
        v-show="advancedOptionsShown"
        class="arrow down"
      />
    </div>
    <div v-show="advancedOptionsShown">
      <label
        class="option-label"
        :for="qid+'_default'"
      >Valor por defeito: </label>
      <input
        :id="qid+'_default'"
        v-model="this.result.default"
        type="datetime-local"
        @change="update"
      >
      <br>
      <label
        class="option-label"
        :for="qid+'_begin'"
      >Data mínima: </label>
      <input
        :id="qid+'_begin'"
        v-model="this.result.begin"
        type="datetime-local"
        @change="update"
      >
      <br>
      <label
        class="option-label"
        :for="qid+'_end'"
      >Data máxima: </label>
      <input
        :id="qid+'_end'"
        v-model="this.result.end"
        type="datetime-local"
        @change="update"
      >
    </div>
  </div>
</template>

<script>
export default {
  name: "DateTimeField",
  props: ["qid", "properties"],
  emits: ["update"],
  data() {
    return {
      result: this.properties,
      valid: true,
      invalid: "",
      advancedOptionsShown: false,
    };
  },
  methods: {
    update() {
      this.valid = !this.result.begin || !this.result.end || this.result.begin <= this.result.end
      if (this.result.default === "")
        this.result.default = undefined;
      if (this.result.begin === "")
        this.result.begin = undefined;
      if (this.result.end === "")
        this.result.end = undefined;
      this.$emit("update", this.result);
    },
  },
};
</script>

<style type="scss">
  .option-label {
    display: inline-block;
    width: 145px;
    white-space: nowrap;
  }
</style>
