<template>
  <div v-if="added_sections !== undefined">
    <h1>Criação de novo formulário</h1>
    <form
      id="form-area"
      class="flex column"
      @submit.prevent="submit"
    >
      <div>
        <input
          id="title"
          v-model="title"
          class="seamless_input"
          type="text"
        >
        <hr>
        <h3><label for="type">Tipo de formulário</label></h3>
        <select
          id="type"
          v-model="type"
          required
        >
          <option disabled />
          <option
            v-for="formType in form_types"
            :key="formType.id"
            :value="formType.id"
          >
            {{ formType.name }}
          </option>
        </select>
      </div>
      <br>
      <div
        id="advanced_options_control"
        class="flex row unselectable"
        @click="advancedOptionsShown = !advancedOptionsShown"
      >
        <h2>Opções avançadas</h2>
        <i
          v-show="!advancedOptionsShown"
          class="arrow right"
        /><i
          v-show="advancedOptionsShown"
          class="arrow down"
        />
      </div>
      <div
        v-show="advancedOptionsShown"
        id="advanced_options"
      >
        <br>
        <h3><label for="submit_message">Mensagem de sucesso</label></h3>
        <textarea
          id="submit_message"
          v-model="submit_message"
        />
        <br><br>
        <h3>
          <label
            class="label"
            for="redirect"
          >Página de redireção após sucesso</label>
        </h3>
        <input
          id="redirect"
          v-model="redirect"
          type="text"
        >
        <br><br>
        <h3><label for="redirect_delay">Atraso de redireção (segundos)</label></h3>
        <input
          id="redirect_delay"
          v-model="redirect_delay"
          type="number"
          step="0.25"
        >
        <br v-if="editable">
        <br v-if="editable">
        <label
          v-if="editable"
          class="switch"
        >
          <input
            v-model="hide_section_numbers"
            type="checkbox"
          >
          <span class="slider round hide_sections" />
        </label>
        <br><br>
        <label
          v-if="editable"
          class="switch"
        >
          <input
            v-model="hide_question_numbers"
            type="checkbox"
          >
          <span class="slider round hide_questions" />
        </label>
      </div>
      <hr>
      <template v-if="editable">
        <div
          v-for="(section, sectionIndex) in added_sections"
          :key="sectionIndex"
          class="flex row width-100 section"
        >
          <div style="margin-left: 5px;margin-right: 10px;">
            <i
              class="remove round flex ca vca unselectable"
              @click="added_sections.splice(sectionIndex, 1)"
            >✖</i>
            <i
              v-show="hasPrevSection(sectionIndex)"
              class="arrow up"
              @click="moveSectionUp(sectionIndex)"
            /><br>
            <i
              v-show="hasNextSection(sectionIndex)"
              class="arrow down"
              style="bottom:7px"
              @click="moveSectionDown(sectionIndex)"
            />
          </div>
          <div class="flex column">
            <input
              v-model="section.section_name"
              class="seamless_input"
              type="text"
            >
            <br>
            <div
              v-for="(field, fieldIndex) in section.questions"
              :key="fieldIndex"
              class="flex row la width-100 question"
            >
              <div style="margin-left: 5px;margin-right: 10px;">
                <i
                  class="remove round flex ca vca unselectable"
                  @click="section.questions.splice(fieldIndex, 1)"
                >✖</i>
                <i
                  v-show="hasPrev(sectionIndex, fieldIndex)"
                  class="arrow up"
                  @click="moveUp(sectionIndex, fieldIndex)"
                /><br>
                <i
                  v-show="hasNext(sectionIndex, fieldIndex)"
                  class="arrow down"
                  style="bottom:7px"
                  @click="moveDown(sectionIndex, fieldIndex)"
                />
              </div>
              <div>
                <div>
                  <h3>{{ getFieldTypeTitle(field.properties.field_type) }}</h3>
                  <br>
                  <input
                    :id="'section_'+sectionIndex+'question_'+fieldIndex"
                    v-model="field.question"
                    type="text"
                    class="seamless_input"
                  >
                </div>
                <br>
                <label
                  v-if="!field.properties.field_type.includes('_exposition')"
                  class="switch"
                >
                  <input
                    v-model="field.required"
                    type="checkbox"
                  >
                  <span class="slider round" />
                  <br>
                </label>
                <component
                  :is="field.properties.field_type"
                  :key="field.properties"
                  class="flex column ca vla"
                  :question="field"
                  :qid="sectionIndex+'_'+fieldIndex"
                  :properties="field.properties"
                  @update="field.properties = $event"
                />
              </div>
            </div>
            <div class="new_field flex row ca">
              <div
                v-show="selecting_field[sectionIndex]"
                class="flex row la"
              >
                <select
                  v-model="new_field"
                  class="flex row ca add_field"
                  @change="selected_field(section, sectionIndex)"
                >
                  <option
                    disabled
                    value="none"
                  >
                    Campo de Texto
                  </option>
                  <option value="shorttext">
                    Texto Curto
                  </option>
                  <option value="longtext">
                    Texto Longo
                  </option>
                  <option disabled>
                    Email
                  </option>
                  <option disabled>
                    Morada
                  </option>
                </select>
                <select
                  v-model="new_field"
                  class="flex row ca add_field"
                  @change="selected_field(section, sectionIndex)"
                >
                  <option
                    disabled
                    value="none"
                  >
                    Campo Numérico
                  </option>
                  <option value="number">
                    Número
                  </option>
                  <option disabled>
                    NIF
                  </option>
                  <option disabled>
                    Número telefónico
                  </option>
                </select>
                <select
                  v-model="new_field"
                  class="flex row ca add_field"
                  @change="selected_field(section, sectionIndex)"
                >
                  <option
                    disabled
                    value="none"
                  >
                    Campo de Data
                  </option>
                  <option value="date">
                    Data
                  </option>
                  <option value="datetime">
                    Data e Hora
                  </option>
                </select>
                <select
                  v-model="new_field"
                  class="flex row ca add_field"
                  @change="selected_field(section, sectionIndex)"
                >
                  <option
                    disabled
                    value="none"
                  >
                    Campo de Seleção
                  </option>
                  <option value="multiplechoice">
                    Escolha Múltipla
                  </option>
                  <option value="selectboxes">
                    Caixas de Seleção
                  </option>
                </select>
                <select
                  v-model="new_field"
                  class="flex row ca add_field"
                  @change="selected_field(section, sectionIndex)"
                >
                  <option
                    disabled
                    value="none"
                  >
                    Campo de Exposição
                  </option>
                  <option value="text_exposition">
                    Descrição
                  </option>
                  <option value="image_exposition">
                    Imagem
                  </option>
                  <option value="youtube_exposition">
                    Vídeo (YouTube)
                  </option>
                </select>
              </div>
              <button
                v-show="!selecting_field[sectionIndex]"
                type="button"
                class="add_field round_add hoverable unselectable"
                @click="selecting_field[sectionIndex] = !selecting_field[sectionIndex]"
              >
                +
              </button>
            </div>
          </div>
        </div>
        <div />
        <div class="flex row ca">
          <button
            type="button"
            class="add_field add_section hoverable"
            @click="addSection()"
          >
            Adicionar secção
          </button>
        </div>
      </template>
      <h3 v-else>
        O formulário já tem {{responses}} resposta<template v-if="responses != 1">s</template>, as perguntas não podem ser alteradas. Pode no entando alterar as opções acima.
      </h3>
      <hr>
      <div
        v-if="response === undefined"
        class="flex row ca"
        style="height: 70px;"
      >
        <a
          class="add_field add_section hoverable cancel"
          href="/forms"
        >
          Cancelar
        </a>
        <button
          type="submit"
          class="add_field add_section hoverable submit"
        >
          Submeter
        </button>
      </div>
      <template v-else>
        <h3
          v-if="response.id !== undefined || (!editable && response.title !== undefined)"
          class="flex row ca"
          style="height: 70px;"
        >
          Formulário atualizado com sucesso. Pode continuar a editar.
        </h3>
        <div v-else>
          Houve um erro interno na submissão do formulário.<br>
          <textarea v-model="response" />
        </div>
      </template>
    </form>
  </div>
</template>

<script>
import * as components from "@/fieldCreators";
import { API } from "@/http-common";
export default {
  name: "EditForm",
  components,
  data() {
    return {
      response: undefined,
      form_types : [],
      title: "Título do Formulário",
      type: undefined,
      form: {},
      submit_message : "Formulário submetido com sucesso!",
      redirect: "/forms/",
      redirect_delay : 3,
      advancedOptionsShown : false,
      new_field : "none",
      selecting_field : [],
      added_sections: undefined,
      hide_section_numbers: false,
      hide_question_numbers: false,
      editable: false,
      responses: 0,
    };
  },
  created() {
    API.get("formType/")
      .then((response) =>
        this.form_types = response.data
      );
    API.get("form/".concat(this.$route.params.id))
      .then((response) => {
        var data = response.data;
        this.title = data.title;
          this.type = data.type;
          this.added_sections = data.form.sections;
          this.hide_section_numbers = data.form.hide_section_numbers;
          this.hide_question_numbers = data.form.hide_question_numbers;
          this.submit_message = data.submit_message;
          this.redirect = data.redirect;
          this.redirect_delay = data.redirect_delay;
          this.editable = data.responses == 0;
          this.responses = data.responses;
      })
  },
  methods: {
    addSection() {
      var init = {"section_name" : "Título de Secção", "questions" : []}
      this.added_sections.push(init);
    },
    selected_field(section, sectionIndex) {

      this.selecting_field[sectionIndex] = !this.selecting_field[sectionIndex];
      section.questions.push({"question" : "Título", "properties": {"field_type" : this.new_field}});
      this.new_field = "none";
    },
    submit() {
      if (confirm('Confirma que a informação introduzida está correta?')) {
        for (var i = 0; i < this.added_sections.length; /* do nothing */) {
          if (this.added_sections[i].questions.length == 0)
            this.added_sections.splice(i, 1);
          else
            i++;
        }
        const options = {
          method: "put",
          url: "/form/".concat(this.$route.params.id).concat("/"),
          headers: {
            "X-CSRFToken": this.getCookie("csrftoken"),
          },
          data: {
            title: this.title,
            type: this.type,
            form: this.editable ? { "hide_section_numbers" : this.hide_section_numbers,
                    "hide_question_numbers" : this.hide_question_numbers,
                    "sections" : this.added_sections} : undefined,
            submit_message: this.submit_message,
            redirect: this.redirect,
            redirect_delay : this.redirect_delay,
          },
          transformResponse: [
            async (data) => {
              try {
                this.response = JSON.parse(data);
              } catch (e) {this.response = data;}
              if (this.response.id !== undefined || (!this.editable && this.response.title !== undefined)) {
                await new Promise(r => setTimeout(r, 5000));
                this.response = undefined;
              }
              return data;
            },
          ],
        };
        API(options);
      }
    },
    getCookie(name) {
      let cookieValue = null;
      if (document.cookie && document.cookie !== "") {
        const cookies = document.cookie.split(";");
        for (let i = 0; i < cookies.length; i++) {
          const cookie = cookies[i].trim();
          if (cookie.substring(0, name.length + 1) === name + "=") {
            cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
            break;
          }
        }
      }
      return cookieValue;
    },
    getFieldTypeTitle(field_type) {
      if (field_type == 'youtube_exposition')
        return "Vídeo (YouTube)"
      else if (field_type == 'text_exposition')
        return "Descrição de texto"
      else if (field_type == 'image_exposition')
        return "Imagem"
      else if (field_type == 'date')
        return "Data"
      else if (field_type == 'datetime')
        return "Data e hora"
      else if (field_type == 'longtext')
        return "Texto Longo"
      else if (field_type == 'shorttext')
        return "Texto Curto"
      else if (field_type == 'multiplechoice')
        return "Escolha Múltipla"
      else if (field_type == 'selectboxes')
        return "Caixas de Seleção"
      else if (field_type == 'number')
        return "Número"
      return ""
    },
    hasPrev(sectionIndex, questionIndex) {
      return questionIndex > 0 ||
             sectionIndex > 0;
    },
    hasNext(sectionIndex, questionIndex) {
      return questionIndex+1 < this.added_sections[sectionIndex].questions.length ||
             sectionIndex+1 < this.added_sections.length;
    },
    moveUp(sectionIndex, questionIndex) {
      if (this.hasPrev(sectionIndex, questionIndex)) {
        var other;
        if (questionIndex > 0) {
          other = this.added_sections[sectionIndex].questions[questionIndex-1];
          this.added_sections[sectionIndex].questions[questionIndex-1] = this.added_sections[sectionIndex].questions[questionIndex];
          this.added_sections[sectionIndex].questions[questionIndex] = other;
        }
        else {
          this.added_sections[sectionIndex-1].questions.push(this.added_sections[sectionIndex].questions[questionIndex]);
          this.added_sections[sectionIndex].questions.splice(questionIndex, 1);
        }
      }
    },
    moveDown(sectionIndex, questionIndex) {
      if (this.hasNext(sectionIndex, questionIndex)) {
        var other;
        if (questionIndex+1 < this.added_sections[sectionIndex].questions.length) {
          other = this.added_sections[sectionIndex].questions[questionIndex+1];
          this.added_sections[sectionIndex].questions[questionIndex+1] = this.added_sections[sectionIndex].questions[questionIndex];
          this.added_sections[sectionIndex].questions[questionIndex] = other;
        }
        else {
          this.added_sections[sectionIndex+1].questions.unshift(this.added_sections[sectionIndex].questions[questionIndex]);
          this.added_sections[sectionIndex].questions.splice(questionIndex, 1);
        }
      }
    },
    hasPrevSection(sectionIndex) {
      return sectionIndex > 0;
    },
    hasNextSection(sectionIndex) {
      return sectionIndex+1 < this.added_sections.length;
    },
    moveSectionUp(sectionIndex) {
      if (this.hasPrevSection(sectionIndex)) {
        var other = this.added_sections[sectionIndex-1];
        this.added_sections[sectionIndex-1] = this.added_sections[sectionIndex];
        this.added_sections[sectionIndex] = other;
      }
    },
    moveSectionDown(sectionIndex) {
      if (this.hasNextSection(sectionIndex)) {
        var other = this.added_sections[sectionIndex+1];
        this.added_sections[sectionIndex+1] = this.added_sections[sectionIndex];
        this.added_sections[sectionIndex] = other;
      }
    }
  },
};
</script>

<style lang="scss">
  #form-area {
    width: 100%;
  }
  .no-bottom {
    padding-bottom: 0;
  }

  .invalid {
    background-color: #e99;
    padding: 5px;
  }

  .requiredticker {
    color: #b11;
  }

  .seamless_input {
    font-size: 1.5em;
    border: none;
    padding-bottom: 3px;
    border-bottom: 1px solid #555;
    background-color: rgba(0, 0, 0, 0.025);
  }

  .seamless_input:focus {
    //border: 1px solid black;
    background-color: rgba(0, 0, 0, 0.1);
  }

  #advanced_options_control {
    cursor: pointer;
  }

  .arrow {
    border: solid #333;
    border-width: 0 3px 3px 0;
    margin-top: 12px;
    cursor: pointer;
  }

  .right {
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
  }

  .left {
    transform: rotate(135deg);
    -webkit-transform: rotate(135deg);
  }

  .up {
    transform: rotate(-135deg);
    -webkit-transform: rotate(-135deg);
  }

  .down {
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
  }

  .unselectable {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }

  .add_field {
    max-width: 200px;
    height: 50px;
    border: none;
    border-radius: 10px;
    color: black;
    font-size: 1.1em;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.5);
    transition: all 0.15s ease 0s;
    margin-bottom: 20px;
    cursor: pointer;
    margin-right: 20px;
  }

  .round_add {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius : 100%;
    width: 48px;
    height: 48px;
    font-size: 32px;
    padding-bottom: 6px;
    font-weight: bolder;
  }

  .hoverable:hover {
    background-color: #3273dc;
    color: #fff;
    box-shadow: 0px 8px 15px rgba(50, 115, 220, 0.5);
    transform: translateY(-2px);
  }

  .submit:hover {
    background-color: #080;
    box-shadow: 0px 8px 15px rgba(0, 128, 0, 0.5);
  }

  .cancel {
    background-color: rgb(239, 239, 239);
  }

  .cancel:hover {
    background-color: #800;
    box-shadow: 0px 8px 15px rgba(128, 0, 0, 0.5);
  }

  /* The switch - the box around the slider */
  .switch {
    position: relative;
    display: inline-block;
    width: 40px;
    height: 16px;
  }

  /* Hide default HTML checkbox */
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }

  /* The slider */
  .slider {
    position: absolute;
    margin-left: 6px;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
  }

  .slider:before {
    position: absolute;
    content: "";
    height: 24px;
    width: 24px;
    left: -4px;
    bottom: -4px;
    background-color: white;
    -webkit-transition: .2s;
    transition: .2s;
    box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.5);
  }

  .slider:after {
    content: "Obrigatório";
    position: absolute;
    left: 48px;
    bottom: -4px;
        white-space: nowrap;
  }

  .hide_sections:after {
    content: "Esconder números de secção";
  }

  .hide_questions:after {
    content: "Esconder números de questão";
  }

  input:checked + .slider {
    background-color: #3273dc;
  }

  input:focus + .slider {
    box-shadow: 0 0 1px #3273dc;
  }

  input:checked + .slider:before {
    -webkit-transform: translateX(18px);
    -ms-transform: translateX(18px);
    transform: translateX(18px);
  }

  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }

  .slider.round:before {
    border-radius: 50%;
  }

  .question {
    border: 1px solid #ddd; 
    margin-bottom: 20px; 
    background-color: rgba(0, 0, 0, 0.05);
    box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.15);
    border-radius: 5px;
  }

  .section {
    border: 1px solid #ddd; 
    margin-bottom: 20px; 
    background-color: rgba(0, 0, 0, 0.01);
    box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.15);
  }

  .remove {
    cursor: pointer;
    font-style: normal;
    color: white;
    background-color: #a00;
    width: 32px;
    height: 32px;
    border-radius: 100%;
    font-weight: bolder;
    font-size: 18px;
    padding-top: 1px;
    margin-top: 12px;
    margin-left: 6px;
  }

  .add_section {
    font-size: 1.1em;
    font-weight: bold;
    color: #333;
    padding: 10px;
  }
</style>
